<template>
  <div id="navigationHeader">
    <div class="navigationHeader">
      <router-link @click.native="goback()" to="">
        <div v-if="type == 'black'" style="color: black;">
          <img :src="'/static/img/left-arrow-key-black.svg'" alt="" width="20px"> Back
        </div>
        <div v-if="type == 'white'" style="color: white;">
          <img :src="'/static/img/left-arrow-key-white.svg'" alt="" width="20px"> Back
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  name: "navigationHeader",
  props: ['type'],
  data: () => ({

  }),
  methods: {
    goback() {
      this.$router.go(-1);
    }
  }
}
</script>
<style lang="scss" scoped>
.navigationHeader{

  padding: 10px;

  img{
    vertical-align: bottom;
  }
}
</style>



