<template>
  <div class="Songrow row align-items-center">
    <div class="col-1 "><span class="sno">{{ sno + 1 }}</span></div>
    <div class="col">
      <h2>{{ songName }}</h2>
      <p>{{ artist }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Songrow",
  props: ['songName','artist','sno'],
  data: () => ({
  })
}
</script>
<style lang="scss" scoped>
.Songrow{
  padding: 5px 0;
  border-bottom: solid 1px rgba(0,0,0,0.1);

  .sno{
    color: rgba(0,0,0,0.5);
    background-color: rgba(0,0,0,0.08);
    border-radius: 50%;
    font-size: 12px;
    line-height: 20px;
    width: 20px; height: 20px;
    display: inline-block; text-align: center;
  }
  .coverImage{
    border-radius: 50px;
  }
  h2{
    margin: 0; padding: 0;
    font-size: 18px;
    font-weight: 100;
  }
  p{
    margin: 0; padding: 0;
    font-size: 14px;
    color: rgba(0,0,0,0.4);
  }
}

</style>



