<template>
  <div id="about">

    <div class=" aboutContainer">
      <navigation-header style="text-align: left;" type="black"></navigation-header>
      <div class="row align-items-center"  style="height:100%;">
        <div class="col">
          <img src="/static/img/logo.svg" alt="Desi Records Logo" width="120px">
          <h1>Desi Records</h1>
          <p class="version">v0.2</p>
          <p>Desi Records is a punjabi music directory with songs from many different sources</p>
          <a href="https://github.com/heyarviind/desirecords" target="_blank">Github</a>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
import navigationHeader from './NavigationHeader';

export default {
  name: "about",
  data: () => ({

  }),
  components: {
    'navigation-header' : navigationHeader
  }
}
</script>
<style lang="scss" scoped>
.aboutContainer{
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background-color: #fff;
  width: 100%; height: 100%;
  text-align: center;

  h1{
    margin: 0; padding: 0;
    font-size: 26px;
    font-weight: 100;
  }
  p.version{
    margin: 0; padding: 0;
    font-size: 14px; color: rgba(0,0,0,0.6);
  }
}
</style>



